<template>
  <div class="text-wrapper">
    <div class="text">
      {{text}}</div>
    
  </div>
</template>

<script>
export default {
  props:{
    text:String
  }
}
</script>

<style lang="less" scoped>
.text-wrapper{
  width: 700px;
  .text{
    display:-webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow:ellipsise;
    font-size: 13px;
    color: dimgray;
  }
}
</style>