<template>
  <div class="singListPage" v-if="playlist">
    <div class="head">
      <div class="img">
        <img :src="picUrl" />
      </div>
      <div class="desc">
        <div class="name">
          <span class="sp1">专辑</span>
          <span class="spName">{{ playlist.name }}</span>
        </div>
        <div class="author">
          <span class="name-wrapper">
            <span class="authorName">{{ playlist.singername }}</span>
            <span class="time">{{ playlist.aDate }}</span>
          </span>
        </div>
        <div class="play-wrapper">
          <van-button
            round
            type="primary"
            icon="play"
            color="#1ed2a8"
            @click="playAll"
            >播放全部</van-button
          >
          <van-button
            type="default"
            round
            icon="send-gift-o"
            @click="onStore"
            v-if="!isStore"
            style="width: 110px"
            >收藏</van-button
          >
          <van-button
            type="default"
            round
            icon="send-gift-o"
            @click="onStore"
            v-else
            style="width: 110px"
            >取消收藏</van-button
          >
          <van-button type="default" round icon="share-o">分享</van-button>
          <van-button type="default" round icon="down">下载全部</van-button>
        </div>
        <div class="tag">
          <p>
            标签:
            <span style="color: #5091ca" class="spTags">{{
              playlist.genre
            }}</span>
          </p>
          <p>
            <span>歌曲: {{ playlist.total_song_num }}</span>
          </p>
        </div>

        <div class="info">
          <OverText :text="playlist.desc" class="overText" />
          <!-- {{ playlist.desc }} -->
        </div>
      </div>
    </div>
    <div class="body">
      <div class="body-top">
        <div class="text">
          <span class="text1" @click="switchBar" :class="show ? 'ml' : ''"
            >歌曲列表</span
          >
          <span class="text2" @click="switchBar" :class="show ? '' : 'ml'"
            >评论({{ commenttotal }})</span
          >
          <!-- <span class="text3 ml">收藏者</span> -->
        </div>
      </div>
      <div class="list" v-if="!show">
        <div class="list-top">
          <div class="top-item operator">操作</div>
          <div class="top-item title">歌曲</div>
          <div class="top-item singer">歌手</div>
          <div class="top-item cd">专辑</div>
          <div class="top-item time">时间</div>
        </div>
        
        <div
          class="list-top sing"
          v-for="(item, index) in playlist.list"
          :key="index"
          @click="playSong(item)"
          :class="item.songid == playingList.id ? 'active' : ''"
        >
          <div class="top-item operator">
            <span>{{ String(index + 1).padStart(2, "0") }}</span>
            <van-icon name="like-o" />
            <van-icon name="down" />
          </div>
          <div class="top-item title">
            {{ item.songname }}
            <span
              v-if="item.pay.pay_play"
              class="addition"
              style="color: #07c160"
            >
              VIP</span
            >
            <span
              class="addition"
              style="color: #ff976a"
              v-if="item.albumid % 2"
            >
              SQ</span
            >
            <span v-if="item.mv"> <i class="iconfont icon-mv"></i></span>
          </div>
          <div class="top-item singer">
            <span v-for="(al, index) in item.singer" :key="al.id">{{
              index == item.singer.length - 1 ? al.name : al.name + "/"
            }}</span>
          </div>
          <div class="top-item cd">{{ item.albumname }}</div>
          <div class="top-item time">
            {{
              String(Math.floor(item.interval / 60)).padStart(2, "0") +
              ":" +
              String(Math.floor(item.interval % 60)).padStart(2, "0")
            }}
          </div>
        </div>
      </div>
      <div class="comment" v-else>
        <p>精彩评论</p>
        <div
          class="comment-item"
          v-for="item in commentlist"
          :key="item.commentid"
        >
          <Comment :commentData="item" />
          <van-divider :style="{ borderColor: '#969799', padding: '0 19px' }" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Comment from "@/components/Comment";
import { onBeforeMount, reactive, ref, toRefs, watch } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import OverText from "../components/overText.vue";
export default {
  components: {
    Comment,
    OverText,
  },
  setup(props, ctx) {
    const state = reactive({
      playlist: {},
      songUrl: "",
      commentlist: [],
      commenttotal: 0,
      show: false,
      songList: [],
      isStore: false,
      picUrl: "",
    });
    const playerRef = ref(null);
    const switchBar = () => {
      state.show = !state.show;
    };
    const onStore = () => {
      state.isStore = !state.isStore;
    };
    const router = useRouter();
    const id = router.currentRoute._value.query.id;
    const store = useStore();
    const playAll = () => {
      for (let i = state.playlist.list.length - 1; i >= 0; i--) {
        playSong(state.playlist.list[i]);
      }
      setTimeout(() => {
        ctx.emit("playSong", true);
      }, 0);
    };
    const playSong = (e) => {
      for (let i = 0; i < store.state.playList.length; i++) {
        if (store.state.playList[i].id == e.songid) {
          store.commit("setPlayIndex", i);
          return;
        }
      }
      let obj = {};
      obj.id = e.songid;
      obj.songName = e.songname;
      obj.author = e.singer[0].name;
      obj.interval = e.interval;
      // obj.songmid = e.
      obj.coverPic =
        "https://y.qq.com/music/photo_new/T001R300x300M000" +
        e.singer[0].mid +
        ".jpg?max_age=2592000";

      obj.songUrl =store.state.playList.length?store.state.playList[Math.floor(Math.random()*store.state.playList.length)].songUrl:
        "http://isure.stream.qqmusic.qq.com/M500004adg8z18YoUI.mp3?guid=227137925&vkey=005EABF7BA8543EA705F978E22A3869A6219D34C32A6BC24E7E2BC778688F5F5960547B3E692B39B1B12CC4255A5AF947083473C1F153760&uin=&fromtag=66";
      store.commit("setPlayList", obj);
      setTimeout(() => {
        ctx.emit("playSong", true);
      }, 1000);
    };
    onBeforeMount(() => {
      state.songList = store.state.playList;
      axios({
        url: `/api/getAlbumInfo?albummid=${id}`,
        withCredentials: true, //关键
      }).then((res) => {
        state.playlist = res.data.response.data;
        console.log(res.data.response.data);
        state.picUrl =
          "https://y.qq.com/music/photo_new/T002R300x300M000" +
          state.playlist.mid +
          ".jpg?max_age=2592000";
      });
      axios({
        url: `/api/getComments?id=${id % 10000}`,
        withCredentials: true, //关键
      }).then((res) => {
        if (res.data.response.comment.commenttotal) {
          console.log(res.data.response.comment);
          state.commentlist = res.data.response.comment.commentlist;
          state.commenttotal = res.data.response.comment.commenttotal;
        } else {
          axios({
            url: `/api/getComments?id=${7007}`,
            withCredentials: true, //关键
          }).then((res) => {
            console.log(res.data.response.comment);
            state.commentlist = res.data.response.comment.commentlist;
            state.commenttotal = res.data.response.comment.commenttotal;
          });
        }
      });
    });
    return {
      ...toRefs(state),
      playSong,
      switchBar,
      playAll,
      onStore,
      playerRef,
    };
  },
  emits: ["playSong"],
  computed: {
    playingList() {
      const store = useStore();
       watch(
        () => store.state.playList,
        (newVal) => {
          store.state.playList = newVal
        },
        {  deep: true }
      );
       watch(
        () => store.state.playIndex,
        (newVal) => {
         store.state.playIndex = newVal
        },
        { deep: true }
      );

      return store.state.playList[store.state.playIndex] || { id: 0 };
    },
  },
};
</script>

<style lang="less" scoped>
.singListPage {
  width: 1042px;
  .head {
    width: 100%;
    margin: 35px;
    display: flex;
    .img {
      img {
        width: 210px;
        border-radius: 10px;
      }
    }
    .desc {
      margin-left: 50px;
      .name {
        .sp1 {
          display: inline-block;
          padding: 0px 3px;
          color: #1ed2a8;
          border: 1px solid #1ed2a8;
        }
        .spName {
          margin-left: 10px;
          font-size: 22px;
          font-weight: bold;
        }
      }
      .author {
        margin-top: 11px;
        position: relative;
        img {
          border-radius: 50%;
          width: 25px;
        }
        .identityIcon {
          width: 11px;
          position: absolute;
          left: 16px;
          bottom: 3px;
        }
      }
      .name-wrapper {
        margin-left: 10px;
        position: relative;
        bottom: 6px;
        .authorName {
          font-size: 10px;
          color: #439ac1;
        }
        .time {
          font-size: 10px;
          margin-left: 4px;
          color: #646566;
        }
      }
      .play-wrapper {
        display: flex;
        margin-top: 10px;
        margin-left: -11px;
        button {
          height: 30px;
          margin-left: 10px;
        }
        .tag {
          font-size: 14px;
          p {
            .spTags {
              color: #5091ca;
            }
          }
        }
        .info {
          font-size: 14px;
          width: 700px;
          height: 62px;
          overflow: hidden;
          .sp1 {
            font-size: 18px;
          }
          .overText {
            width: 700px;
          }
        }
      }
    }
  }
  .body {
    .body-top {
      display: flex;
      font-size: 15px;
      justify-content: space-between;
      margin: 20px 30px;
      .text {
        font-size: 18px;
        font-weight: bold;
        cursor: pointer;
        .text1 {
        }
        .text2 {
          margin-left: 30px;
        }
        .ml {
          font-size: 15px;
          font-weight: normal;
        }
      }
    }
    .list {
      .list-top {
        display: flex;
        // justify-content: space-around;
        color: #969799;
        font-size: 14px;
        margin: 0 16px;
        .top-item {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          height: 50px;
          line-height: 50px;
        }
        .top-item:hover {
          background-color: #dcdee0;
        }
        .operator {
          text-align: end;
          width: 130px;
          margin-right: 14px;
        }
        .title {
          width: 400px;
          height: 50px;
          line-height: 50px;
          display: flex;
          .addition {
            display: block;
            height: 15px;
            font-size: 10px;
            line-height: 15px;
            margin-left: 8px;
            margin-top: 17px;
            border: 1px solid;
            padding: 0 3px;
            box-sizing: border-box;
          }
          .icon-mv {
            display: block;
            margin-left: 8px;
          }
        }
        .singer {
          width: 200px;
        }
        .cd {
          width: 256px;
        }
        .time {
          width: 160px;
        }
      }
      .sing {
        color: #111;
        cursor: pointer;
        .operator {
          color: #afb0b0;
          i {
            font-size: 20px;
            margin: 0 8px;
          }
        }
      }
      .active {
        color: #1ed2a9;
      }
      .sing:nth-child(2n) {
        background-color: #f0f1f2;
      }
      .sing:hover {
        background-color: #dcdee0;
      }
    }
    .comment {
      padding: 31px;
      background-color: #f7f8fa;
      p {
        margin-top: 15px;
      }
      .comment-item {
        margin-left: -20px;
      }
    }
  }
}
</style>